import React from 'react';
import { Grid, Icon, List, Header, Divider, Segment } from 'semantic-ui-react';
import Slider from '../components/Slider2';
import { Link } from 'react-router-dom';

const SingleProjectSui = ({projects, images}) => {

    const projectsListIcons = projects.listIcons
    const projectsAddListIcons = projects.addlistIcons

    const ListItems = projectsListIcons.map((item) => {
        return (
            <List.Item key={item.listHeader} className="no-pointer">
            <Icon name={item.listIcon} size='big' />
            <List.Content className="no-pointer">
              <List.Header>{item.listHeader}</List.Header>
              <List.Description>
              {item.listDescr}
              </List.Description>
            </List.Content>
          </List.Item>
        );
      });

      const additionalItems = projectsAddListIcons.map((item) => {
        return (
            <List.Item key={item.listHeader}> 
            <Icon name={item.listIcon} size='big' className={item.listLink !== 'not available' ? "link" : null} />
            <List.Content>
              <List.Header>
              {item.listLink !== 'not available'
              ? <Link to={{ pathname: item.listLink }} target="_blank" className="link">{item.listHeader}</Link>
              : item.listHeader}
              </List.Header>
              <List.Description>
              {item.listLink !== 'not available'
              ? <Link to={{ pathname: item.listLink }} target="_blank" className="link">{item.listDescr}</Link>
              : <span style={{whiteSpace: 'pre-wrap'}}>{item.listDescr}</span>}
              </List.Description>
            </List.Content>
          </List.Item>
        )
      });
      
    const additionalDividerAndItemList = (
        <>
        <Divider horizontal>{projects.addDividerKeywords}</Divider>
        {additionalItems}
        </>
    )

    const ProjectContent = (
      <Grid columns={2} divided stackable padded='horizontally' >
        <Grid.Row divided centered >
                <Header as='h2'>
                  <Icon name={projects.headerIcon} />
                <Header.Content>
                  {projects.headerContent}
                </Header.Content>
                </Header> 
              </Grid.Row>
      <Grid.Row>
      <Grid.Column className={window.innerWidth > 600 ? 'projectSlider' : ''}>
         <Slider items = {images} />
      </Grid.Column>
     <Grid.Column>
          <Grid.Row divided>
</Grid.Row>
  <Divider horizontal>Keywords</Divider>
    <Header as='h3'>{projects.dividerKeywords}</Header> 
       <List animated selection size="big">
       {ListItems}
       {projects.isAddDivider === true 
       ? additionalDividerAndItemList
       : null}
  </List>
      </Grid.Column>
    </Grid.Row> 
    </Grid>
      )

    return (
<>

<Segment 
padded="very" className='projectBg'>
  {ProjectContent}
</Segment> 

</>
     );
}
 
export default SingleProjectSui;