import React, { useContext } from 'react';
import { StoreContext } from '../Store/StoreProvider';
import SingleProjectSui from './SingleProjectSui';
import { Grid, Segment, Icon, Header, Divider } from 'semantic-ui-react';

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';


const ProjectsSui = () => {

    const  { 
      desertShopProjects, 
      desertShopImages, 
      pCOWebProjects, 
      pCOWebImages, 
      hmiProjects, 
      hmiImages, 
      vueProjects, 
      vueImages,
      automProjects,
      automImages,
      unsplashImages,
      unsplashProjects
    } = useContext(StoreContext);

    return (
<>
<Segment vertical className='segmentBoxShadow'>

<Grid inverted stackable padded centered vertical>
          <Grid.Row >
            <Grid.Column widescreen={13}>
              <Grid.Row divided>

                <Header inverted as='h2'>
                  <Icon name='folder open' />
                  <Header.Content>Projects</Header.Content>
                </Header> 

              </Grid.Row>
               <Grid.Row>

                <Divider horizontal inverted>Projects</Divider>

                {/* <Carousel 
                autoPlay={false} 
                infiniteLoop={true} 
                interval='5000' 
                showArrows={true} 
                showThumbs={false} 
                showStatus={false} 
                showIndicators={true} 
                useKeyboardArrows 
                emulateTouch={false}
                > */}
              
            <SingleProjectSui first={true}
              left={true} right={false}
              projects={desertShopProjects}
              images={desertShopImages}
            />   
            
            <SingleProjectSui
              left={false} right={true}
              projects={unsplashProjects}
              images={unsplashImages}
            />

            <SingleProjectSui
              left={true} right={false}
              projects={pCOWebProjects}
              images={pCOWebImages}
            />

            <SingleProjectSui
              left={false} right={true}
              projects={hmiProjects}
              images={hmiImages}
            />

            <SingleProjectSui
              left={true} right={false}
              projects={vueProjects}
              images={vueImages}
            />

            <SingleProjectSui
              left={false} right={true}
              projects={automProjects}
              images={automImages}
            />

            {/* </Carousel> */}
            
</Grid.Row>
</Grid.Column>
</Grid.Row>
</Grid>

</Segment>
</>
     );
}
 
export default ProjectsSui;